.video-container {
    background: url("./images/img-home.jpg")
    center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.video-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.video-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.video-button {
    margin-top: 32px;
}

.video-button .button {
    margin: 6px;
}

.fa-play-circle {
    margin-left: 4px;
}

@media screen and (max-width: 960px) {
    .video-container > h1 {
    font-size: 70px;
    margin-top: -150px;
    }
};

@media screen and (max-width: 768px) {
    .video-container > h1 {
        font-size: 50px;
        margin-top: -100px;
    }
};

    .video-container > p {
        font-size: 30px;
        text-align: center;
    }

    /* reserved */
    .button-mobile {
        display: block;
        text-decoration: none;
    }

    .button {
        width: 100%;
    }